ottom: 30px;\n  }\n  @include mobile {\n    margin-bottom: 40px;\n    margin-left: 10px;\n  }\n}\n\n.main--apply--wrapper {\n  position: relative;\n  display: flex;\n  background-color: rgb(237, 238, 239);\n  margin-bottom: 70px;\n  @include desktop {\n    width: 700px;\n    height: 250px;\n  }\n  @include mobile {\n    width: 100%;\n    flex-direction: column;\n    align-items: center;\n  }\n}\n\n.main--apply--create_modal_button {\n  position: absolute;\n  right: 0px;\n  top: -30px;\n  span {\n    @include medium_kor;\n    &:hover {\n      cursor: pointer;\n      font-size: 16px;\n    }\n    @include mobile {\n      margin-right: 10px;\n    }\n  }\n}\n\n.main--apply--list {\n  overflow-y: scroll;\n  &::-webkit-scrollbar {\n    display: none;\n  }\n  @include desktop {\n    width: $list_width;\n    padding: $list_wrapper_padding;\n  }\n  @include mobile {\n    width: 100%;\n    height: 250px;\n    padding: $list_wrapper_padding 10px;\n  }\n}\n\n.main--apply--list--title {\n  @include medium_kor;\n  font-weight: 500;\n  margin: 0;\n  @include desktop {\n    font-size: 18px;\n  }\n  @include mobile {\n    font-size: 16px;\n  }\n}\n\n.main--apply--list--guide {\n  @include medium_kor;\n  @include desktop {\n    position: absolute;\n    left: 25%;\n    top: 37%;\n  }\n  @include mobile {\n    padding-top: 30px;\n  }\n}\n\n.main--apply--eventInfo {\n  position: relative;\n  @include desktop {\n    width: $basic_width - $list_width;\n    border-left: 1px solid #50505057;\n    padding: $list_wrapper_padding;\n  }\n  @include mobile {\n    width: 100%;\n    height: 250px;\n    border-top: 1px solid #50505057;\n    padding: $list_wrapper_padding 10px;\n  }\n}\n\n.main--apply--eventInfo--title_wrapper {\n  @include medium_kor;\n  display: flex;\n  justify-content: space-between;\n  align-items: baseline;\n  font-size: 16px;\n  font-weight: 500;\n  margin: 0;\n  margin-bottom: 10px;\n  padding-bottom: 5px;\n  border-bottom: 1px solid #50505057;\n  @include desktop {\n  }\n  @include mobile {\n  }\n}\n\n.main--apply--eventInfo--title {\n  max-width: 230px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n.main--apply--eventInfo--maker {\n  @include small_kor;\n  margin: 0;\n  vertical-align: bottom;\n}\n\n.main--apply--eventInfo--description {\n  @include medium_kor;\n  font-size: 14px;\n  margin: 0;\n  margin-bottom: $list_wrapper_padding;\n  max-height: 140px;\n  overflow-y: scroll;\n  &::-webkit-scrollbar {\n    display: none;\n  }\n}\n\n.main--apply--eventInfo--submit {\n  @include medium_kor;\n  color: $red_for_submit;\n  position: absolute;\n  bottom: $list_wrapper_padding;\n  right: $list_wrapper_padding;\n  &:hover {\n    cursor: pointer;\n    transform: scale(1.1);\n  }\n}\n\n.attended {\n  color: $darkgrey_default;\n}\n\n.main--apply--eventInfo--guide {\n  @include medium_kor;\n  position: relative;\n  @include desktop {\n    top: 18%;\n    left: 5%;\n  }\n}\n","@import '@css/fonts.scss';\n\n@mixin main--wrappers {\n  width: 600px;\n  margin: 0 auto;\n}\n\n@mixin main--titles {\n  // font-weight: 300;\n  font-family: $kor_font_regular;\n  font-size: 16px;\n  color: $darkgrey_default;\n}\n","// family\n$kor_font_regular: 'Noto Sans KR', sans-serif;\n$eng_font_regular: 'Roboto', sans-serif;\n\n// color\n$darkgrey_darker: #252323;\n$darkgrey_default: rgb(80, 80, 80);\n$darkgrey_medium: rgb(140, 140, 140);\n$darkgrey_light: rgb(184, 184, 184);\n$white: white;\n$lightblue: #73b2ed;\n$pinkred: #ec7063;\n$red_for_submit: #9f062ba3;\n\n//size\n$main_title_22: 22px;\n$sub_title_18: 18px;\n$default_medium_15: 15px;\n$default_small_13: 13px;\n\n// font-set-default\n@mixin medium_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n@mixin medium_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n// font-set-white\n@mixin big_kor_white {\n  font-family: $kor_font_regular;\n  font-size: $sub_title_18;\n  color: $white;\n}\n\n@mixin medium_kor_white {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $white;\n}\n"],"sourceRoot":""}]);
